import React from 'react';
import './App.css';
import { Explorer } from "./components/Explorer";
import ExplorerType from './data/ExplorerType';
import { Octokit } from 'octokit';


export const octokit = new Octokit({
  auth: "26da14dd6fcac59312fe6eac0deb356d472667f4"
});


let tree: ExplorerType = {};

const traverse = (parent: ExplorerType, path: Array<string>, data: ExplorerType): object => {
  const entry = path[0];

  if (!parent.hasOwnProperty(entry)) {
    parent = { ...parent, [entry]: data.mode === "100644" ? data : {}};
  }

  if (path.length > 1) {
    const node = traverse(parent[entry], path.slice(1), data);
    parent = { ...parent, [entry]: node };
  }

  return parent;
}


const fetchGitHubReops = async (): Promise<ExplorerType> => {
  const repos = await octokit.request("GET /repos/{owner}/{repo}/commits?sha=main", {
    owner: "imdev912",
    repo: "imdev912.github.io"
  });

  if (repos.status === 200) {
    const data = repos.data;
    const commit = data[0];
    const sha: string = commit.sha;

    const files = await octokit.request("GET /repos/{owner}/{repo}/git/trees/{tree_sha}?recursive=true", {
      owner: "imdev912",
      repo: "imdev912.github.io",
      tree_sha: sha
    });

    if (files.status === 200) {
      const repoData = files.data.tree;

      repoData.forEach((item: ExplorerType) => {
        const path = item.path ? item.path.split('/') : [];
        tree = traverse(tree, path, item);
      });

      return tree;
    }
  }

  return {};
}


const sortObj = (obj: ExplorerType): ExplorerType => {
  return Object.keys(obj).sort().reduce((result: ExplorerType, key) => {
    let data = obj[key];

    if (typeof data === "object") {
      data = sortObj(data);
    }

    result[key] = data;
    return result;
  }, {});
}


function App() {
  const [explorer, setExplorer] = React.useState<ExplorerType>({});

  React.useEffect(() => {
    (async () => {
      const repoData = await fetchGitHubReops();
      const repos = sortObj(repoData);
      setExplorer(repos);
    })();
  }, []);

  return (
    <div className="App">
      <Explorer explorer={explorer} expand={true} />
    </div>
  );
}

export default App;
