import React from "react";
import { Folder, FolderOpen, Link } from "@mui/icons-material";
import ExplorerType from "../data/ExplorerType";
import { Explorer } from "./Explorer";


interface Props extends ExplorerType {
	"show"?: boolean
}


export const FolderView = (data: Props) => {
	const [expand, setExpand] = React.useState<boolean>(false);

	const toggleExpand = () => {
		if (data.hasOwnProperty("index.html")) {
      window.open(`https://imdev912.github.io/${data["index.html"]["path"].replace("index.html", '')}`, "_blank");
		} else {
      setExpand((prev) => !prev);
    }
	};

	return (
		<>
			<div
				className={`folder ${data.show ? "show" : "hide"}`}
				onClick={toggleExpand}
			>
				{
          data.hasOwnProperty("index.html") ? (
            <Link
              sx={{
                color: "blue", fontSize: "16px", marginRight: "5px", transform: "rotate(-45deg)" }}
            />
          ) : (
            expand ? (
              <FolderOpen
                sx={{ color: "red", fontSize: "16px", marginRight: "5px" }}
              />
            ) : (
              <Folder
                sx={{ color: "red", fontSize: "16px", marginRight: "5px" }}
              />
            )
          )
				}

				{data.path}
			</div>

			<div className="items">
				<Explorer explorer={data} expand={expand} />
			</div>
		</>
	);
};
