import React from "react";
import ExplorerType from "../data/ExplorerType";
import { FolderView } from "./FolderView";
import { FileView } from "./FileView";
import { nanoid } from "nanoid";


interface Props {
	explorer: ExplorerType,
	expand: boolean,
	setExpand?: React.Dispatch<React.SetStateAction<boolean>>
}


export const Explorer = (props: Props): React.ReactElement => {
	const { explorer, expand } = props;

	return (
		<>
			{
				Object.keys(explorer).map((key) => {
					const data = explorer[key];

					if (typeof data === "object") {
						if (data.hasOwnProperty("mode")) {
							// return <FileView key={nanoid()} {...data} show={expand} />;
						} else {
              if (!["index.html", "explorer"].includes(key)) {
                return <FolderView key={nanoid()} {...data} path={key} show={expand} />;
              }
						}
					}
				})
			}
		</>
	);
};
